.main{
    margin: 0px;
    padding: 0px;
    width: 100vw;
    height: 100vh;
    background-image: url('../images/back1.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    transition: 1s;
    
    animation-name: animate;
    animation-direction: alternate-reverse;
    animation-duration: 10s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-play-state: running;
    animation-timing-function: ease-in-out;
}
@keyframes animate{
    0%{background-image: url('../images/back1.jpg');}
    50%{background-image: url('../images/back2.jpg');}
    100%{background-image: url('../images/back3.jpg');}
}
.search{
    margin-top: 15%;
    width: 30%;
    height: 7vh;
    outline-style: none;
    font-size: 1.2rem;
    border: none;
    box-shadow: 0px 0px 10px 1px;
    border-radius: 10rem;
    padding-left: 2%;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    background-color: rgb(255, 251, 251,0.7);
}

.result{
    position: relative;
    text-align: center;
    width: 40%;
    height: 50vh;
    margin-top: 2%;
    background-color: rgb(255, 251, 251,0.7);
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 1.2rem;
    border: none;
    box-shadow: 0px 0px 10px 1px;
    border-radius: 5rem;

}
.temp{
    font-size: 8rem;
}
.country{
position: absolute;
bottom: 0;
left: 30%;
right: 30%;
font-size:small;
}
.region{
    font-size:small;

}

@media only screen and (max-width:480px){
    .search{
        margin-top: 15%;
        width: 70%;
        height: 2.5rem;
        outline-style: none;
        font-size: 1.2rem;
        border: none;
        box-shadow: 0px 0px 10px 1px;
        border-radius: 10rem;
        padding-left: 2%;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        background-color: rgb(255, 251, 251,0.7);
    }
    .result{
        position: relative;
        text-align: center;
        width: 70%;
        height: 50vh;
        margin-top: 10%;
        background-color: rgb(255, 251, 251,0.7);
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        border: none;
        box-shadow: 0px 0px 10px 1px;
        border-radius: 2rem;
    
    }
    .temp{
        font-size: 5rem;
    }
}
